ter {\n    width: 100%;\n    height: rem(1);\n    background-color: $black;\n    position: absolute;\n    top: 50%;\n    transform: translateY(-50%);\n    left: 0;\n    content: \"\";\n    display: block;\n    z-index: -1;\n  }\n}\n\nol,\nul {\n  .article__body & {\n    margin-left: 0;\n\n    li {\n      list-style: none;\n      padding-left: $pad;\n      text-indent: rem(-10);\n\n      &::before {\n        color: $primary-color;\n        width: rem(10);\n        display: inline-block;\n      }\n\n      li {\n        list-style: none;\n      }\n    }\n  }\n}\n\nol {\n  .article__body & {\n    counter-reset: item;\n\n    li {\n      &::before {\n        content: counter(item) \". \";\n        counter-increment: item;\n      }\n\n      li {\n        counter-reset: item;\n\n        &::before {\n          content: \"\\002010\";\n        }\n      }\n    }\n  }\n}\n\nul {\n  .article__body & {\n    li {\n      &::before {\n        content: \"\\002022\";\n      }\n\n      li {\n        &::before {\n          content: \"\\0025E6\";\n        }\n      }\n    }\n  }\n}\n\narticle {\n  margin-left: auto;\n  margin-right: auto;\n\n  p a,\n  li a {\n    text-decoration: underline !important;\n  }\n\n  li p {\n    margin-top: $space;\n    text-indent: 0;\n  }\n}\n\nbody#tinymce,\n.article__body {\n  p,\n  ul,\n  ol,\n  dt,\n  dd {\n    @include p;\n  }\n\n  strong {\n    font-weight: bold;\n  }\n\n  > p:empty,\n  > h2:empty,\n  > h3:empty {\n    display: none;\n  }\n\n  > h1,\n  > h2,\n  > h3,\n  > h4 {\n    margin-top: $space-double;\n\n    &:first-child {\n      margin-top: 0;\n    }\n  }\n\n  h1,\n  h2 {\n    + * {\n      margin-top: $space-and-half;\n    }\n  }\n\n  h3,\n  h4,\n  h5,\n  h6 {\n    + * {\n      margin-top: $space-half;\n    }\n  }\n\n  img {\n    height: auto;\n  }\n\n  hr {\n    margin-top: $space-half;\n    margin-bottom: $space-half;\n\n    @include media(\">large\") {\n      margin-top: $space;\n      margin-bottom: $space;\n    }\n  }\n\n  figcaption {\n   