medium') {\n    margin-bottom: 0;\n  }\n\n  a {\n    color: $white;\n  }\n}\n\n.footer--inner {\n  width: 100%;\n}\n\n.footer--left {\n  @include media('>medium') {\n    width: 50%;\n  }\n\n  @include media('>xlarge') {\n    width: 33.33%;\n  }\n}\n\n.footer--right {\n  display: flex;\n  flex-direction: column;\n\n  > div {\n    @include media('>xlarge') {\n      flex-direction: row;\n      padding-right: $pad;\n    }\n  }\n\n  @include media('>medium') {\n    width: 50%;\n    flex-direction: row;\n  }\n\n  @include media('>xlarge') {\n    width: 66.67%;\n  }\n}\n\n.footer__row {\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n\n  &--bottom {\n    align-items: flex-start;\n    padding-right: $pad-double;\n  }\n\n  @include media('>medium') {\n    &--top {\n      flex-direction: row;\n    }\n  }\n\n  @include media('>large') {\n    flex-direction: row;\n    justify-content: space-between;\n  }\n}\n\n.footer__nav {\n  display: flex;\n  justify-content: flex-start;\n  align-items: flex-start;\n  flex-direction: row;\n}\n\n.footer__nav-col {\n  display: flex;\n  flex-direction: column;\n  padding-right: $pad;\n\n  @include media('>large') {\n    padding-right: $pad-double;\n  }\n\n  > * {\n    margin-bottom: rem(15);\n  }\n}\n\n.footer__nav-link {\n  @include font--primary--s;\n\n  white-space: nowrap;\n\n  &:hover {\n    opacity: 0.8;\n  }\n}\n\n.footer__mailing {\n  max-width: rem(355);\n\n  input[type=\"text\"] {\n    background-color: transparent;\n  }\n}\n\n.footer__copyright {\n  text-align: left;\n  order: 1;\n\n  @include media('>large') {\n    order: 0;\n  }\n}\n\n.footer__social {\n  order: 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  .icon {\n    padding: $pad-half;\n    display: block;\n    width: rem(40);\n    height: auto;\n\n    &:hover {\n      opacity: 0.8;\n    }\n  }\n}\n\n.footer__posts {\n  margin-top: $space;\n\n  @include media('>medium') {\n    margin-top: 0;\n  }\n}\n\n.footer__ads {\n  margin-top: $space-double;\n  width: rem(360);\n\n  @include media('>medium') {\n    display: none;\n  }\n\n  @include media('>xlarge') {\n    display: block;\n    margin-top: 0;\n  }\n}\n\n.footer__top {\n  position: absolute;\n  right: rem(-55);\n  bottom: rem(60);\n  padding: $pad-half $pad-half $pad-half $pad;\n  display: block;\n  width: rem(150);\n  transform: rotate(-90deg);\n  white-space: nowrap;\n\n  .icon {\n    height: auto;\n    transition: margin-left 0.25s ease;\n  }\n\n  &:hover {\n    .icon {\n      margin-left: $space;\n    }\n  }\n\n  @include media('>large') {\n    bottom: rem(70);\n  }\n}\n","/* ------------------------------------*\\\n    $HEADER\n\\*------------------------------------ */\n\n.header__utility {\n  display: flex;\n  height: rem($utility-header-height);\n  width: 100%;\n  position: fixed;\n  z-index: 99;\n  align-items: center;\n  flex-direction: row;\n  justify-content: space-between;\n  overflow: hidden;\n  border-bottom: 1px solid #4a4a4a;\n\n  a:hover {\n    opacity: 0.8;\n  }\n}\n\n.header__utility--left {\n  display: none;\n\n  @include media('>large') {\n    display: flex;\n  }\n}\n\n.header__utility--right {\n  display: flex;\n  justify-content: space-between;\n  width: 100%;\n\n  @include media('>large') {\n    justify-content: flex-end;\n    width: auto;\n  }\n}\n\n.header__utility-search {\n  width: 100%;\n}\n\n.header__utility-mailing {\n  display: flex;\n  align-items: center;\n  padding-left: $pad-half;\n\n  .icon {\n    height: auto;\n  }\n}\n\n.header__utility-social {\n  display: flex;\n  align-items: flex-end;\n\n  a {\n    border-left: 1px solid #4a4a4a;\n    width: rem($utility-header-height);\n    height: rem($utility-header-height);\n    padding: $pad-half;\n\n    &:hover {\n      background-color: rgba(black, 0.8);\n    }\n  }\n}\n\n.header__nav {\n  position: relative;\n  width: 100%;\n  top: rem($utility-header-height);\n  z-index: 999;\n  background: $white;\n  height: rem($small-header-height);\n\n  @include media('>large') {\n    height: rem($large-header-height);\n    position: relative;\n  }\n\n  &.is-active {\n    .nav__primary-mobile {\n      display: flex;\n    }\n\n    .nav__toggle-span--1 {\n      width: rem(25);\n      transform: rotate(-45deg);\n      left: rem(-12);\n      top: rem(6);\n    }\n\n    .nav__toggle-span--2 {\n      opacity: 0;\n    }\n\n    .nav__toggle-span--3 {\n      display: block;\n      width: rem(25);\n      transform: rotate(45deg);\n      top: rem(-8);\n      left: rem(-12);\n    }\n\n    .nav__toggle-span--4::after {\n      content: \"Close\";\n    }\n  }\n}\n\n.header__logo-wrap a {\n  width: rem(100);\n  height: rem(100);\n  background-color: $white;\n  border-radius: 50%;\n  position: relative;\n  display: block;\n  overflow: hidden;\n  content: \"\";\n  margin: auto;\n  transition: none;\n\n  @include media('>large') {\n    width: rem(200);\n    height: rem(200);\n  }\n}\n\n.header__logo {\n  width: rem(85);\n  height: rem(85);\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  margin: auto;\n  display: block;\n\n  @include media('>large') {\n    width: rem(170);\n    height: rem(170);\n  }\n}\n","/* ------------------------------------*\\\n    $MAIN CONTENT AREA\n\\*------------------------------------ */\n\n.search .alm-btn-wrap {\n  display: none;\n}\n\n$bp-black: #141416;\n$bp-red: #fb2640;\n$bp-gray: #9b9b9b;\n$bp-ff-primary: \"industry\", sans-serif;\n$bp-ff-secondary: \"din-2014\", sans-serif;\n\n.body-party {\n  background: $bp-black;\n\n  // Main Layout\n  .main {\n    padding: 0;\n    margin: 0;\n  }\n\n  .article {\n    position: relative;\n    height: 100%;\n    min-height: 100vh;\n    padding: $space-double $space;\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    align-items: stretch;\n    align-content: stretch;\n  }\n\n  // Buttonss\n  .btn {\n    display: block;\n    letter-spacing: 1px;\n\n    @include media(\">small\") {\n      width: 100%;\n      margin-left: auto;\n      margin-right: auto;\n    }\n  }\n\n  .btn--red {\n    background-color: $bp-red;\n\n    &:hover {\n      background-color: darken($bp-red, 10%);\n    }\n  }\n\n  .btn--outline {\n    border-color: $bp-gray;\n    color: $bp-gray;\n    padding-left: $space;\n    padding-right: $space;\n  }\n\n  // Headings\n  h1 {\n    font-family: $bp-ff-primary;\n    font-size: 34px;\n    line-height: 1.2;\n    letter-spacing: 0;\n    text-align: center;\n  }\n\n  h2 {\n    font-family: $bp-ff-primary;\n    font-size: 20px;\n    line-height: 1.2;\n    letter-spacing: 0;\n    text-align: center;\n  }\n\n  h3 {\n    font-family: $bp-ff-primary;\n    font-size: 14px;\n    line-height: 1;\n    letter-spacing: 0;\n    text-align: center;\n    color: white;\n  }\n\n  h4 {\n    font-family: $bp-ff-secondary;\n    color: white;\n    text-transform: uppercase;\n    text-align: left;\n    font-weight: bold;\n  }\n\n  p,\n  p a {\n    font-family: $bp-ff-secondary;\n    text-align: center;\n    color: $bp-gray;\n    line-height: 1.4;\n  }\n\n  .kicker {\n    color: $bp-red;\n    font-family: $bp-ff-secondary;\n    border-bottom: 1px solid white;\n  }\n}\n\n.page-birthday-party {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  height: 100%;\n\n  .article {\n    &__title {\n      display: flex;\n      align-items: center;\n      height: 100%;\n      flex: 1 1 auto;\n    }\n\n    &__buttons {\n      display: flex;\n      flex-direction: column;\n    }\n  }\n}\n\n.page-birthday-party__decline {\n  .article {\n    &__title {\n      position: relative;\n      min-height: 50vh;\n      display: flex;\n      align-items: center;\n      justify-content: center;\n\n      &::after {\n        content: \"\";\n        display: block;\n        width: 200px;\n        height: 200px;\n        border-radius: 50%;\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        transform: translate(-50%, -50%);\n        border: 1px solid $bp-red;\n      }\n    }\n\n    &__dot {\n      .dot {\n        width: 15px;\n        height: 15px;\n        border-radius: 50px;\n        background-color: $bp-red;\n        display: block;\n        margin: $space-half auto 0 auto;\n      }\n    }\n  }\n}\n\n.page-birthday-party__invite {\n  .layout-container {\n    max-width: 100%;\n  }\n\n  .article {\n    max-width: 600px;\n    margin: 0 auto;\n    padding-top: 100px;\n\n    &__alert {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      background-color: $bp-red;\n      color: white;\n      text-align: center;\n      padding: $space-half;\n\n      small {\n        font-size: 12px;\n      }\n\n      p {\n        color: white;\n        font-weight: bold;\n        font-size: 18px;\n      }\n    }\n\n    &__body {\n      position: relative;\n      padding-bottom: 200px;\n      background: url(\"../../assets/images/bp-cup.png\") bottom center no-repeat;\n\n      &::before {\n        content: \"\";\n        display: block;\n        width: 1px;\n        height: calc(100% - 100px);\n        background-color: $bp-gray;\n        position: absolute;\n        top: 0;\n        left: 50%;\n        transform: translateX(-50%);\n        z-index: -1;\n      }\n\n      .person {\n        margin-left: -$space;\n        margin-right: -$space;\n\n        &-row {\n          width: 50%;\n          position: relative;\n          margin-bottom: $space-double;\n\n          &::before {\n            content: \"\";\n            display: block;\n            width: 20px;\n            height: 20px;\n            border-radius: 50px;\n            border: 1px solid $bp-red;\n            background-color: $bp-black;\n            position: absolute;\n            top: 0;\n            z-index: 1;\n          }\n\n          &::after {\n            content: \"\";\n            display: block;\n            width: 6px;\n            height: 6px;\n            border-radius: 50px;\n            background-color: $bp-red;\n            position: absolute;\n            top: 8px;\n            z-index: 1;\n          }\n\n          .kicker {\n            margin-bottom: $space;\n            display: table;\n          }\n\n          h3 {\n            line-height: 1.2;\n          }\n\n          p {\n            margin-top: 5px;\n          }\n\n          &:nth-child(even) {\n            > * {\n              text-align: right;\n              margin-right: 30px;\n            }\n\n            &::before {\n              right: -11px;\n            }\n\n            &::after {\n              right: -3px;\n            }\n\n            .kicker {\n              text-align: right;\n              margin-left: auto;\n            }\n          }\n\n          &:nth-child(odd) {\n            left: 50%;\n\n            > * {\n              text-align: left;\n              margin-left: 30px;\n            }\n\n            &::before {\n              left: -11px;\n            }\n\n            &::after {\n              left: -3px;\n            }\n          }\n        }\n      }\n    }\n\n    &__details {\n      display: flex;\n      flex-direction: column;\n\n      &--row {\n        border-bottom: 1px solid $bp-gray;\n        padding: $space-half 0;\n        display: flex;\n\n        &:last-child {\n          border-bottom: 0;\n        }\n\n        h4 {\n          padding-right: $space;\n          width: 100px;\n        }\n\n        p {\n          text-align: left;\n        }\n      }\n    }\n\n    &__buttons {\n      display: flex;\n      flex-direction: column;\n      width: 100%;\n\n      @include media(\">small\") {\n        max-width: 200px;\n        margin-left: auto;\n        margin-right: auto;\n      }\n    }\n  }\n}\n\n.page-birthday-party__rsvp {\n  .article {\n    justify-content: flex-start;\n  }\n\n  input {\n    border-bottom: 1px solid $bp-gray;\n    background-color: transparent;\n    border-top: 0;\n    border-left: 0;\n    border-right: 0;\n    padding-left: 0;\n    color: white;\n  }\n\n  .wpcf7-list-item,\n  .wpcf7-list-item-label {\n    text-align: left;\n    top: -4px;\n    position: relative;\n  }\n\n  .wpcf7-response-output {\n    color: white;\n  }\n\n  form {\n    max-width: 500px;\n    margin: 0 auto;\n  }\n\n  input[type=radio] {\n    border: 1px solid $bp-red;\n    box-shadow: 0;\n  }\n\n  input[type=radio]:checked {\n    background-color: $bp-red;\n  }\n\n  .wpcf7-form-control-wrap.attending {\n    display: block;\n\n    .wpcf7-list-item {\n      margin-top: 15px;\n    }\n\n    &::before {\n      content: \"Are you attending?\";\n      text-align: left;\n      display: block;\n      margin-bottom: 5px;\n    }\n  }\n\n  input[type=submit] {\n    background-color: $bp-red;\n    text-align: center;\n    box-shadow: none;\n    border: 0;\n    padding-left: $space;\n    padding-right: $space;\n    width: 100%;\n\n    @include media(\">small\") {\n      max-width: 200px;\n    }\n  }\n}\n","/* ------------------------------------*\\\n    $ANIMATIONS & TRANSITIONS\n\\*------------------------------------ */\n","/* ------------------------------------*\\\n    $BORDERS\n\\*------------------------------------ */\n\n.border {\n  border: 1px solid $border-color;\n}\n\n.divider {\n  height: rem(1);\n  width: rem(60);\n  background-color: $gray;\n  display: block;\n  margin: $space auto;\n  padding: 0;\n  border: none;\n  outline: none;\n}\n","/* ------------------------------------*\\\n    $COLOR MODIFIERS\n\\*------------------------------------ */\n\n/**\n * Text Colors\n */\n.color--white {\n  color: $white;\n  -webkit-font-smoothing: antialiased;\n}\n\n.color--off-white {\n  color: $off-white;\n  -webkit-font-smoothing: antialiased;\n}\n\n.color--black {\n  color: $black;\n}\n\n.color--gray {\n  color: $gray;\n}\n\n/**\n * Background Colors\n */\n.no-bg {\n  background: none;\n}\n\n.background-color--white {\n  background-color: $white;\n}\n\n.background-color--off-white {\n  background-color: $off-white;\n}\n\n.background-color--black {\